.popup__input {
  width: 358px;
  margin: 30px auto 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-size: 14px;
  line-height: 17px;
  padding: 0 0 13px;
  display: flex;
  justify-content: center;
  outline: none;
}

@media (max-width: 530px) {
  .popup__input {
    width: 238px;
  }
}